<template>
  <div>
    <Modal v-model="receiveIndoModal" width="400">
      <p slot="header" style="text-align: center; font-size: 20px; color: cornflowerblue;">
        <span>ReceiveInfo</span>
      </p>
      <Card
      v-for="receiveInfo in receiveInfoList"
      :key="receiveInfo.id"
      style="margin:0 auto;width: 100%;margin-top: 10px; ">
        <h3 slot="title">ReceiveInfo_ID: {{receiveInfo.receive_info_id}}</h3>
        <div style="display: flex; justify-content: flex-start;">
          <div>
            <h4>TEL:</h4>
          </div>
          <div style="margin-left: 10px;">
            <p>{{receiveInfo.phone_number}}</p>
          </div>
        </div>
        <div style="display: flex; justify-content: flex-start;">
          <div>
            <h4>ADDR:</h4>
          </div>
          <div style="margin-left: 10px;">
            <p>{{receiveInfo.address}}</p>
          </div>
        </div>
      </Card>
    </Modal>
    <Modal v-model="addAccountModal" width="400">
      <p slot="header" style="text-align: center; font-size: 20px; color: cornflowerblue;">
        <span>Create Account</span>
      </p>
      <Form :model="createAccountForm" style="margin:auto">
        <FormItem label="AccountId">
          <Input
          v-model="createAccountForm.account_id"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="15"
          :autosize="{minRows: 1}"></Input>
        </FormItem>
        <FormItem label="Password">
          <Input
          v-model="createAccountForm.password"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="6"
          :autosize="{minRows: 1}"></Input>
        </FormItem>
        <FormItem label="Nickname">
          <Input
          v-model="createAccountForm.nickname"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="15"
          :autosize="{minRows: 1}"></Input>
        </FormItem>
      	<FormItem label="Gender">
      		<RadioGroup v-model="createAccountForm.gender">
      			<Radio :label="1">Male</Radio>
      			<Radio :label="2">Female</Radio>
            <Radio :label="3">Unknown</Radio>
      		</RadioGroup>
      	</FormItem>
      	<FormItem label="Description">
      		<Input
          v-model="createAccountForm.description"
          show-word-limit
          type="textarea"
          placeholder="Enter something..."
          maxlength="200"
      		:autosize="{minRows: 2}"></Input>
      	</FormItem>
      </Form>
      <div slot="footer">
      	<Button type="primary" size="large" long @click="submit()">Submit</Button>
      </div>
    </Modal>
    <h1 style="margin-top:10px; text-align: center;">Account</h1>
    <Table height="500" border :columns='columns' :data='accountData' style="margin-top: 20px;">
      <template slot-scope="{ row, index }" slot="account_id">
        <Input type="text" v-model="editAccount_id" show-word-limit maxlength="15" v-if="editIndex === index" />
        <span v-else>{{ row.account_id }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="password">
        <Input type="text" v-model="editPassword" show-word-limit maxlength="6" v-if="editIndex === index" />
        <span v-else>{{ row.password }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="nickname">
        <Input type="text" v-model="editNickname" show-word-limit maxlength="15" v-if="editIndex === index" />
        <span v-else>{{ row.nickname }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="gender">
        <Input type="text" v-model="editGender" show-word-limit maxlength="2" v-if="editIndex === index" />
        <span v-else>{{ row.gender }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="description">
        <Input type="text" v-model="editDescription" show-word-limit maxlength="200" v-if="editIndex === index" />
        <span v-else>{{ row.description }}</span>
      </template>

      <template slot-scope="{ row, index }" slot="action">
        <div v-if="editIndex === index">
          <Button type="success" size="small" style="margin-right: 5px" @click="handleSave(index)">保存</Button>
          <Button type="warning" size="small" @click="editIndex = -1">取消</Button>
        </div>
        <div v-else>
          <Button type="primary" size="small" style="margin-right: 5px" @click="handleEdit(row, index)">修改</Button>
          <Button type="info" size="small" @click="receiveInfo(index)">收货信息</Button>
          <Button type="error" size="small" @click="remove(index)">移除</Button>
        </div>
      </template>
    </Table>
    <Button type="primary" style="margin-top: 10px; width: 100px;" v-on:click="createAccount">
      <Icon type="ios-add" size="20"></Icon>
      Add
    </Button>
    <Button type="info" style="margin-top: 10px; margin-left: 10px; width: 100px;" v-on:click="upLoad">
      <Icon type="ios-cloud" size="20"></Icon>
      Upload
    </Button>
  </div>
</template>

<script>
  //import data from '../mock/account.js'
  //import data2 from '../mock/receiveInfo.js'
  export default {
    props:{
      dataSource: Number
    },
    data() {
      return {
        receiveIndoModal: false,
        receiveInfoList: [],
        addAccountModal: false,
        createAccountForm: {
          account_id: 'AccountID',
          password: '',
          nickname: 'Username',
          gender: 3,
          description: ''
        },
        columns: [{
            title: 'account_id',
            //slot: 'account_id',
            key: 'account_id',
            sortable: true
          },
          {
            title: 'password',
            slot: 'password',
            sortable: true
          },
          {
            title: 'nickname',
            slot: 'nickname',
            sortable: true
          },
          {
            title: 'gender',
            slot: 'gender',
            sortable: true,
            filters: [{
                label: '男',
                value: 1
              },
              {
                label: '女',
                value: 2
              },
              {
                label: '未知',
                value: 3
              }
            ],
            filterMultiple: false,
            filterMethod(value, row) {
              if (value === 1) {
                return row.gender === '男';
              } else if (value === 2) {
                return row.gender === '女';
              } else {
                return row.gender === '未知';
              }
            }
          },
          {
            title: 'description',
            slot: 'description',
            sortable: true
          },
          {
            title: '操作',
            slot: 'action',
            align: 'center'
          }
        ],
        accountData: [],
        allReceiveInfoList: [],
        editIndex: -1, // 当前聚焦的输入框的行数
        editAccount_id: '',
        editPassword: '',
        editNickname: '',
        editGender: '',
        editDescription: ''
      }
    },
    mounted() {
      //获取所有用户的信息
      var url
      if(this.dataSource == 1){
        url = '/account/info'
      }
      else {
        url = '/g/account/info'
      }
      this.$axios
        .get(url)
        .then(response => {
          this.accountData = response.data
          var index
          for (index = 0; index < this.accountData.length; index++) {
            if (this.accountData[index].gender === 2) {
              this.accountData[index].gender = '女'
            } else if (this.accountData[index].gender === 1) {
              this.accountData[index].gender = '男'
            } else {
              this.accountData[index].gender = '未知'
            }
          }
          this.$Message.success('成功加载Account数据')
        })
        .catch(error => {
          this.$Message.error('加载Account数据失败')
        })

      //获取所有收货信息
      if(this.dataSource == 1){
        url = '/receiveInfo/info'
      }
      else {
        url = '/g/receiveInfo/info'
      }
      this.$axios
          .get(url)
          .then(response=>{
            this.allReceiveInfoList = response.data
            this.$Message.success('成功加载ReceiveInfo数据')
          })
          .catch(error => {
            this.$Message.error('加载ReceiveInfo数据失败')
          })
    },
    methods: {
      upLoad(){
        var url

        //上传account表
        if(this.dataSource == 1) {
          url = '/account/update'
        }
        else {
          url = '/g/account/update'
        }
        var tempList = []
        console.log('test1')
        for(var i = 0; i < this.accountData.length; i++){
          var temp;//性别编号
          console.log('test2')
          if(this.accountData[i].gender === '男') {
            temp = 1
          }
          else if(this.accountData[i].gender === '女'){
            temp = 2
          }
          else {
            temp = 0
          }
          console.log('test3')
          tempList.push({
            account_id:this.accountData[i].account_id,
            password:this.accountData[i].password,
            nickname:this.accountData[i].nickname,
            gender:temp,
            description:this.accountData[i].description
          })
          console.log('test4')
        }
        this.$axios
            .post(url, {
              list: tempList
            })
            .then(response => {
              this.$Message.success("成功将Account数据同步至Hive")
            })
            .catch(error => {
              this.$Message.error("同步Account数据失败！")
            })

        //上传receiveInfo表
        if(this.dataSource == 1) {
          url = '/receiveInfo/update'
        }
        else {
          url = '/g/receiveInfo/update'
        }
        this.$axios
            .post(url, {
              list: this.receiveInfoList
            })
            .then(response => {
              this.$Message.success("成功将ReceiveInfo数据同步至Hive")
            })
            .catch(error => {
              this.$Message.error("同步ReceiveInfo数据失败！")
            })
      },
      submit(){
        //创建Account
        if(this.createAccountForm.gender === 1)
          this.createAccountForm.gender = '男'
        else if(this.createAccountForm.gender === 2)
          this.createAccountForm.gender = '女'
        else
          this.createAccountForm.gender = '未知'
        const newAccount = {
          account_id: this.createAccountForm.account_id,
          password: this.createAccountForm.password,
          nickname: this.createAccountForm.nickname,
          gender: this.createAccountForm.gender,
          description: this.createAccountForm.description
        }
        this.accountData.push(newAccount)
        //自动创建一个ReceiveInfo
        const newReceiveInfo = {
          receiver_account_id: this.createAccountForm.account_id,
          receive_info_id: 0,
          phone_number: '13568904488',
          address: 'Nine eyes bridge'
        }
        this.allReceiveInfoList.push(newReceiveInfo)
        console.log(this.allReceiveInfoList)
        //关闭对话框
        this.addAccountModal = false
      },
      createAccount(){
        this.addAccountModal = true
      },
      remove(index) {
        console.log(this.allReceiveInfoList)
        var account_id = this.accountData[index].account_id
        this.accountData.splice(index, 1)
        for(var i = 0; i < this.allReceiveInfoList.length; ){
          if(this.allReceiveInfoList[i].receiver_account_id == account_id)
            this.allReceiveInfoList.splice(i, 1)
          else
            i++
        }
        console.log(this.allReceiveInfoList)
      },
      handleEdit(row, index) {
        this.editAccount_id = row.account_id;
        this.editPassword = row.password;
        this.editNickname = row.nickname;
        this.editGender = row.gender;
        this.editDescription = row.description;
        this.editIndex = index;
      },
      handleSave(index) {
        this.accountData[index].account_id = this.editAccount_id;
        this.accountData[index].password = this.editPassword;
        this.accountData[index].nickname = this.editNickname;
        this.accountData[index].gender = this.editGender;
        this.accountData[index].description = this.editDescription;
        this.editIndex = -1;
      },
      receiveInfo(index) {
        this.receiveInfoList = []
        var account_id = this.accountData[index].account_id
        for(var i = 0; i < this.allReceiveInfoList.length; i++){
          if(this.allReceiveInfoList[i].receiver_account_id == account_id){
            this.receiveInfoList.push(this.allReceiveInfoList[i])
          }
        }
        this.receiveIndoModal = true
      }
    }
  }
</script>

<style>
</style>



